.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.layout-top-panel {
  height: 5.5rem;
  background-color: #e3dfd4;
  color: #69757e;
  padding: 0 1.6rem;
  display: flex;
  align-items: center;
}

.menu {
  position: relative;
  flex-grow: 1;
  z-index: 200;
}

.menu-btn {
  width: 3.6rem;
  height: 2.7rem;
  border-radius: 0.2rem;
  background-color: #6b757e;
  position: relative;
}

.menu-btn-icon {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 1.2rem;
  height: 1.2rem;
  fill: #e3dfd4;
}

.layout-row {
  display: flex;
  flex-grow: 1;
}

.layout-sidebar {
  flex-shrink: 0;
  background-color: #6b757e;
  display: flex;
  flex-direction: column;
}

.layout-main {
  width: 50%;
  flex-grow: 1;
  background-color: #edeef0;
}

.layout-sidebar.hidden .menu-close {
  transform: translateY(-50%) scaleX(-1);
}

.menu-content {
  width: 24rem;
}

.layout-sidebar.hidden .menu-content {
  display: none;
}

.menu-item {
  padding: 1rem 1.4rem;
  color: white;
  display: block;
  transition: background-color 0.2s, color 0.2s;

  &.hidden {
      display: none;
  }

  &:hover {
    background-color: #c3c6cb;
    color: #6b757e;
    & > .menu-item-dropdown {
      opacity: 1;
      visibility: visible;
      transition: opacity 0.2s, visibility 0.2s;
      z-index: 40;
    }
  }

  .menu-item-dropdown & {
    color: #6b757e;
    &:hover {
      background-color: #dbdce0;
    }
  }
}

.menu-item-dropdown {
  position: absolute;
  left: 100%;
  top: 0;
  bottom: 0;
  background-color: #c3c6cb;
  width: 24rem;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s ease 0.2s, visibility 0s linear 0.4s;
  z-index: 20;
  border-left: 1px solid #6b757e;
}

.menu-item-search {
  background-color: #fff;
  padding: 1rem 1.4rem;

  width: 100%;
  display: block;
  color: #6b757e;
  &::placeholder {
    color: #6b757e;
  }
}

.menu-close {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  height: 7rem;
  width: 2rem;
  background-color: #cfd0d4;
  border-top-left-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem;
  transform-origin: right center;
}

.menu-close-arrow {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  fill: #6b757e;
  width: 1.6rem;
  height: 1.6rem;
}


.graph-filters-download {
  margin-left: auto;
  width: 2rem;
  height: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  background-color: #6b757e;
  border-radius: .2rem;
}


.graph-filters-download-icon {
  width: 80%;
  height: 80%;
  fill: white;
  flex-shrink: 0;
}

