.logout-btn {
  margin-left: auto;
  font-size: 1.4rem;
  line-height: 1.2;
  color: #6b757e;
}

.logout-btn-content {
  display: inline-flex;
  align-items: center;
}

.logout-btn-icon {
  width: 1.8rem;
  height: 1.8rem;
  flex-shrink: 0;
  margin-right: 1rem;
  fill: #6b757e;
}

.page {
  padding: 2rem;
}

.page-heading {
  font-size: 2.4rem;
  margin-bottom: 2rem;
  font-weight: 400;
}

.graph-filters {
  display: flex;
  align-items: center;
}

.graph-period {
  display: flex;
  align-items: center;
  margin-right: 3rem;
}

.graph-period-radio-input {
  display: none;
}

.graph-period-radio {
  border: 1px solid #6b757e;
  border-left: none;
  user-select: none;
  cursor: pointer;

  &:first-child {
    border-top-left-radius: 0.4rem;
    border-bottom-left-radius: 0.4rem;
    border-left: 1px solid #6b757e;
  }

  &:last-child {
    border-top-right-radius: 0.4rem;
    border-bottom-right-radius: 0.4rem;
  }
}

.graph-period-radio-input {
  display: none;
}

.graph-period-radio-text {
  font-size: 1.4rem;
  padding: 0.8rem 1.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  transition: all 0.2s;
  color: #6b757e;
}

.graph-period-radio-input:checked .graph-period-radio {
  background-color: #6b757e;
  color: white;
}

.graph-period-radio-input:checked ~ .graph-period-radio-text {
  background-color: #6b757e;
  color: white;
}

.graph-filters-dates-range {
  background-color: #6b757e;
  padding: 0.6rem;
  border-radius: 0.4rem;
  display: flex;
  align-items: center;
  margin-right: 2rem;
  input {
    background-color: #fff;
    border-radius: 0.4rem;
    padding: 0.4rem;
    font-size: 1.2rem;
  }
}

.graph-filters-dates-range-picker {
  margin-right: 1.5rem;
  display: flex;
  align-items: center;

  &:last-child {
    margin-right: 0;
  }
}

.graph-filters-dates-range-picker-label {
  font-size: 1.2rem;
  margin-right: 0.8rem;
  color: white;
}

.graph-filters-dates-range-picker-wrapper {
  width: 9rem;
}

.graph-filters-dates-select {
  display: flex;
  align-items: center;
  margin-right: 2rem;
  &:last-child {
    margin-right: 0;
  }
}

.graph-filters-dates-select-label {
  margin-right: 1rem;
  font-size: 1.4rem;
  color: #6b757e;
}

.graph-filters-dates-select-wrapper {
  min-width: 20rem;
}

.chart {
  padding: 3rem 0;
}

.loader {
  font-size: 2.4rem;
  font-weight: 400;
}
